.headerCreate {
  background-image: linear-gradient(
    to bottom,
    #e9e9e9 7%,
    #fafafa, );
  display: flex;
  justify-content: center;
  align-items: center;
  padding: rem(31) 0  rem(30) 0;
  height: 12vh;
  @include tablet{
    padding: rem(31) 0 rem(30) rem(40);
    display: flex;
  justify-content: flex-start;
  align-items: center;
  }
  &__img {
    max-width: rem(201);
    max-height: rem(50);
  }
}
